、副餐炒麵」、「吃麵疙瘩、配麵疙瘩湯」等一菜變兩菜的奇怪菜單。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("想重新體檢孩子吃的營養午餐，先試想40元在自助餐能買到怎麼樣的菜色。全台校園營養午餐平均成本不到40元，下方是學生平常營養午餐的餐盤，實際夾菜看看，就知道孩子午餐到底能夠吃到什麼。")])],1),_vm._v(" "),_c('Dish-Game'),_vm._v(" "),_c('Section-Fixed-Bg',{attrs:{"menu-text":"用在食材的錢少","bg":"./static/pic1.jpg","bgweb":"./static/web/pic1.jpg","text":"事實一：繳的錢不全用在食材"}}),_vm._v(" "),_c('Content',{attrs:{"background-color":"#FFFFFF"}},[_c('h3',[_vm._v("成本結構圓餅圖")]),_vm._v(" "),_c('Column-One',{attrs:{"imgsay":"資料來源／台北市餐盒食品商業同業公會","img1":"./static/chart_mobile.svg","imgweb1":"./static/web/chart_web.svg"}}),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("廠商透露，大量購買食材的價格約比一般家庭到菜場購買便宜3成，但廠商還有廚工、設備折舊、水電等開銷，家長繳的錢能有65％用在食材上「已經很高了」。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("就算學校自設廚房，也一樣有經費困窘的問題。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("3年多前，高雄市杉林區集來國小校長潘淑琦剛到任，第一次升旗就被「震撼教育」，目睹學生在她面前癱軟倒地，成為她日後積極爭取午餐經費的動力。她估算，每個學生一餐繳40元，包含老師與行政人員在內，全校每月餐費共6萬元，扣除廚工薪水，每人每餐大約剩25元，只能買一顆肉包。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("台灣小小一塊土地，學校營養午餐22縣市都有自己的規定和做法，價格從一餐最少的34、35元，到最多的60元，差距極大；「營養午餐免費」則時常被候選人當成爭取選票的政治籌碼，逢選舉年就喊出免費口號，但當地方預算拮据時，孩子通常優先被犧牲掉。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("家長的錢沒有全用在食材刀口上、便宜的價格和政策支票，讓台灣營養午餐的結構愈來愈扭曲，不論是外訂團膳或學校自設廚房，都面臨困境。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('a',{attrs:{"href":"https://udn.com/upf/newmedia/2018_data/schoollunch/rural.html","target":"_blank"}},[_c('div',{staticClass:"more"},[_c('img',{attrs:{"src":require("./assets/more1.jpg"),"alt":""}}),_vm._v(" "),_c('div',{staticClass:"more-text"},[_vm._v("挑剩的菜、聘不到廚工"),_c('br'),_vm._v("偏鄉童靠營養午餐撐一天")])])])],1),_vm._v(" "),_c('Section-Fixed-Bg',{attrs:{"menu-text":"畸形的團膳產業","bg":"./static/pic2.jpg","bgweb":"./static/web/pic2.jpg","text":"事實二：廠商衝量求生 孩子的午餐被犧牲"}}),_vm._v(" "),_c('Content',{attrs:{"background-color":"#FFFFFF"}},[_c('h3',[_vm._v("營養午餐三種供餐模式")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('ul',[_c('li',[_