做好隔日要吃的飯。")])],1),_vm._v(" "),_c('Section-Fixed-Bg',{attrs:{"menu-text":"自設廚房的困境","bg":"./static/pic3.jpg","bgweb":"./static/web/pic3.jpg","text":"事實三：學校自設廚房 誰來後援？"}}),_vm._v(" "),_c('Content',{attrs:{"background-color":"#FFFFFF"}},[_c('p',[_vm._v("團膳問題多，學校自己設置廚房就能解決問題嗎？實際走訪各地設有廚房的學校，每個學校幾乎都喊辛苦，沒有廠商願意投標、老師得兼當廚工、修繕費用需東湊西補、沒有營養師把關及開菜單等，都是困境。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("教育部「推動學校午餐專案辦公室」主持人、中興大學教授盛中德直言，越大型的學校，越不願意公辦，校方責任較低，加上沒有獎勵制度，沒有誘因。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("位於新竹縣新豐鄉的埔和國小，是一間全校人數僅250人的小校，學校自設廚房，每天早上7點，廚工、工友開始在廚房中洗米、洗菜，接下來切菜、烹煮，11點30分前料理出三菜一湯，準備配送各班。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('Column-One',{attrs:{"imgsay":"新竹縣埔和國小校內自設廚房，每天早上7點開始作業。","img1":"./static/pic4.jpg","imgweb1":"./static/web/pic4.jpg"}}),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("對小校而言，料理份數較少，料理方式可以更多元，學校自辦廚房也可以縮短運送時間，學生到中午時都可以吃到熱騰騰飯菜。但小校遇到的第一個困境，是食材商招標困難，沒有廠商願意承包經濟規模這麼小的營養午餐。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("埔和國小校長吳柚說，學校經濟規模小，加上對食材的要求高，廠商無利潤可圖，「大廠不願意接、小廠做不了」，去年招標食材商就流標了9次，好不容易才標到一家小廠商。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("今年廠商抱怨虧本、不願再承包，靠學校動之以情、校友自發性捐助補貼廠商成本，才讓已流標7次的營養午餐順利包出去。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('h3',[_vm._v("阿姨請假 老師就得進廚房")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("人事費用、設備維修費用等也都非額外編列的經費，學校需從辦公費裡支出，財務緊繃。廚房人力的不足，讓廚工阿姨吃不消。當阿姨請假或人力困窘時，學校教師、保健室阿姨甚至校長都需進廚房幫忙。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("吳柚指出，學校自設廚房現規定250名學生可以編列一個廚師，但實務運作根本不夠用，「一個廚師要在幾小時內煮出200多人份太困難。」學校因有幼兒園，總共可有兩名廚師，但還是遠遠不足。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('Quote',{attrs:{"color":"#720101","text":"阿姨都做到職業傷害，說想辭職了。","refer":"－埔和國小校長吳柚"}}),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("此外，目前廚房內的人力、設備折舊與維修費用等都非額外補助的經費，學校需要從整體經費內撥，對學校而言是不小負擔。吳柚說，像今年廚房內有設備壞掉，換新的就花了幾萬塊，都得從整體經費裡東拼西湊才能有足夠經費更新。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('Column-One',{attrs:{"imgsay":"學校自設廚房被專家、教師認為是較能確保