長透露，有些菜得靠菜商從山下送來，運費算在菜費中，負擔就會增加。以往菜費到12月才不夠，今年11月就不足，幸好山上學校有許多企業補助，不山不市的學校問題恐更嚴重。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("也有校長透露，在現有制度下，恐有廠商將本求利，為追求利潤，拿CAS肉品混合，透過差價賺取補助款，政府現在沒有防範措施。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("為了配合政策和防弊，每個學校午餐秘書都忙得要命，一條絲瓜要貼一張QRcode，老師一餐要查幾十條絲瓜，連出差都必須請學校廚工用Line把條碼傳送過來，再上網登錄，場面荒謬。")])],1),_vm._v(" "),_c('Section-Fixed-Bg',{attrs:{"menu-text":"怎麼讓孩子吃得好","bg":"./static/pic7.jpg","bgweb":"./static/web/pic7.jpg","text":"三解方 讓孩子吃好吃飽"}}),_vm._v(" "),_c('Content',{attrs:{"background-color":"#FFFFFF"}},[_c('h3',[_vm._v("1. 營養午餐成本結構透明化")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("「價格當然是問題，但誰給我們透明的價格？」民間團體「校園午餐搞非基行動」共同發起人黃嘉琳指出，到現在為止，各縣市沒有人要求廠商、學校公開真實的食材價格以及成本結構，政府從頭到尾都說不清楚購買食材究竟需要多少成本、付多少錢。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('Quote',{attrs:{"color":"#720101","text":"這是學校午餐開辦成功與否的關鍵，若價錢一直無法算清楚，是國家政策、制度上的問題。","refer":"－黃嘉琳"}}),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('h3',[_vm._v("2. 訂定統一規格、價格制訂標準")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("營養午餐目前在22個縣市，可能有22種不同的規格。有些縣市、學校要求4菜1湯，有些學校僅要求2菜1湯；有些要廠商附水果、牛奶，沒有統一規範、卻又是「最有利標」的情況下，讓廠商有太多空間漫天亂喊。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("「我們的小朋友到底需要吃幾種菜，沒人討論過。」黃嘉琳以日本為例，日本的營養午餐是「2菜1汁（湯）」，種類不多但裡面已涵蓋所有孩子需要的營養，也可以藉此教育孩子不要偏食。她也曾隨機去學校班級詢問學生，有1/3孩子舉手認為4菜1湯太多，造成大量廚餘。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("陳明信也認為，目前中央政府沒有太多營養午餐的明確規範，建議政府應統一規格；午餐價格則可透過產官學各界定期依物價等指標檢視，訂定出合理範圍。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("教育部國教署長邱乾國坦承，目前營養午餐大多由地方政府自訂，中央欠缺角色，未來將全面改革，引入專業意見，以免各校定價淪為喊價式，也盼能跳脫一貫「凍漲」的思維。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('h3',[_vm._v("3. 借鏡日韓：國家提供經費照顧孩子午餐")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("不論學校、廠商，台灣談到營養午餐的重要問題癥結都是「價格過低、經費不足」，提高經費是改善第一步。借鏡鄰近的日韓，日本由地方政府全額補助廚房設備、人事成本等費用，家長繳的錢只需用於食材，家長能更清楚掌握午餐品質。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("南韓近年則由民間組織發起「免費營養午餐」運動，截至去年，全國已有79％的學校免費供應營養午餐，經費由地方與中央政府共同分擔。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_vm._v("教育部「推動學校午餐專案辦公室」主持人、中興大學教授盛中德表示，目前國教署考慮參考日本的作法，學生繳交的午餐費用，全部用在食材上，行政費都由政府負擔，今年9月預計先從偏鄉50人以下的學校開始試辦。")]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('a',{attrs:{"href":"https://vision.udn.com/vision/story/12124","target":"_blank"}},[_c('div',{staticClass:"more"},[_c('img',{attrs:{"src":require("./assets/more2.jpg"),"alt":""}}),_vm._v(" "),_c('div',{staticClass:"more-text"},[_vm._v("看更多報導")])])]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('Share',{attrs:{"href":"https://udn.com/upf/newmedia/2018_data/schoollunch/index.html"}}),_vm._v(" "),_c('Logo',{attrs:{"vision":"true"}}),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('Editor',[_c('div',[_vm._v("文字：洪欣慈、連珮宇")]),_vm._v(" "),_c('div',[_vm._v("採訪團隊：何振忠、彭宣雅、魏莨伊、陳雅玲、郭宣彣、徐白櫻、胡蓬生、喻文玟、李京昇、吳佩旻、李樹人、章凱閎、洪欣慈、連珮宇")]),_vm._v(" "),_c('div',[_vm._v("攝影：林伯東、劉學聖")]),_vm._v(" "),_c('div',[_vm._v("視覺設計、影音後製：許瑋琳")]),_vm._v(" "),_c('div',[_vm._v("網頁製作：鄭偉廷")]),_vm._v(" "),_c('div',[_vm._v("監製：何振忠、李曜丞、陳惠惠、王彩鸝、董谷音、潘如瑩、蔡幸怡")]),_vm._v(" "),_c('div',[_vm._v("2018.06.04")])]),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('Relate',{attrs:{"title":"延伸閱讀","color":"#363434","href1":"https://vision.udn.com/vision/story/12124/3159692","img1":"./static/s1.jpg","text1":"營養午餐三部曲：吃得到、吃得好與吃得讚","href2":"https://vision.udn.com/vision/story/12124/3159693","img2":"./static/s2.jpg","text2":"營養午餐幕後...早起分秒搶時間 自設廚房把關品質","href3":"https://vision.udn.com/vision/story/12124/3159718","img3":"./static/s3.jpg","text3":"午餐現場／小校自設廚房 校長也充當廚工","href4":"https://ubrand.udn.com/ubrand/story/11815/3127630","img4":"./static/s4.jpg","text4":"愛吃甜不愛吃飯 營養午餐禁甜湯引論戰"}}),_vm._v(" "),_c('p',[_c('br')]),_vm._v(" "),_c('Fb-Comment',{attrs:{"href":"https://udn.com/upf/newmedia/2018_data/schoollunch/index.html"}})],1),_vm._v(" "),_c('Foot',{attrs:{"background-color":"#FFFFFF"}})],1)}
